html {
  scroll-behavior: smooth;
}

#nav-menu {
  top: 0px;
  position: sticky;
  z-index: 100;
}
.navbar {
  line-height: 45px;
}

#nav-menu > a:hover {
  color: #f56565;
  text-decoration: underline;
}

#img {
  height: 300px;
  width: 200px;
  border-radius: 30px;
  display: block;
  margin: auto;
}
.title {
  font-size: 22px;
  margin: 10px 0px;
  font-weight: 600;
}
.des {
  font-size: 16px;
  font-weight: 450;
  color: #718079;
}
.two {
  /* border: 1px solid goldenrod; */
  /* margin: 10px 0px; */

  margin-right: 10px;
  margin-bottom: 10px;
  padding: 4px 10px;
  border-radius: 6px;
  background-color: #265889;

  font-size: 13px;
  font-weight: 600;
  color: white;
}
.two:hover {
cursor: pointer;
background-color: #4299e1;
}
.rest {
  /* border: 1px solid teal; */
  /* margin: 10px 0px; */
  margin-bottom: 10px;
  margin-right: 10px;
  padding: 4px 10px;
  border-radius: 6px;
 
  background-color: #266467;
  font-size: 13px;
  font-weight: 600;
  color: white;
}
.rest:hover {
  background-color: #10b1b9;
  cursor: pointer;
}
.github {
  /* border: 1px solid teal; */
  padding: 4px 10px;
  border-radius: 6px;
  margin-right: 10px;
  cursor: pointer;
  background-color: #265889;
 
  color: white;
  font-size: 14px;
  font-weight: 600;
}
.github:hover {
  background-color: #4299e1;
  cursor: pointer;
}
.live {
  margin-right: 10px;
  padding: 4px 10px;
  border-radius: 6px;
  cursor: pointer;
  background-color: #2a2a36;
 
  color: white;
  font-size: 14px;
  font-weight: 600;
}

.live:hover {
  background-color: #4d4e55;
  cursor: pointer;

}
.link {
  font-size: 13px;
  margin-left: 5px;
}
.box {
  padding-left: 10px;
  position: relative;
  top: -8px;
}
.imgskill {
  height: 28px;
  width: 28px;
}
.skillitem {
  width: 135px;
  border-bottom: 1px solid #718079;
  /* width: 100%; */
  cursor:pointer;
  padding: 5px 0px;
  margin: 5px 0px;
}
.skillitem:hover {
/* color: #4d4e55; */
background-color: rgb(124, 187, 187);
box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px, rgba(6, 24, 44, 0.65) 0px 4px 6px -1px, rgba(255, 255, 255, 0.08) 0px 1px 0px inset;

  /* border: 1px solid #718079; */

}
